.history {
  width: 1100px;
  border: 1px solid #ccc;
  padding: 25px;
}

.cate .list {
  padding: 25px;
  border: 1px solid #ccc;
  border-radius: 3px;
  overflow: hidden;
  position: relative;
}

.cate .list > b {
  font-size: 20px;
  padding-bottom: 10px;
  display: block;
}

.cate .list aside.h-date {
  overflow: hidden;
}

.cate .list aside.h-date i {
  display: inline-block;
  vertical-align: top;
  margin: 1px 5px 10px 0;
  border: 1px solid #288ad6;
  width: 10px;
  height: 10px;
  padding: 3px;
  overflow: hidden;
  line-height: 10px;
  border-radius: 30px;
  text-align: center;
  font-weight: 600;
  font-size: 11px;
  color: #288ad6;
  font-style: normal;
}

.cate .list aside.h-date span {
  float: right;
  width: calc(100% - 25px);
}

.cate .list table {
  width: 100%;
  word-break: break-word;
}

.cate .list table tr.nobdt td {
  border-top: 0;
  padding-top: 0;
}

.cate .list table tr.btnview td a {
  display: block;
  top: unset;
  margin: 20px auto;
  width: 300px;
  float: unset;
}

.cate .list table .btnview td {
  border-top: unset;
}

.cate .list table th {
  color: #818181;
  padding: 10px 5px;
  font-weight: normal;
  text-align: left;
}

.cate .list table td {
  border-top: 1px solid #e3e3e3;
  padding: 10px 5px;
  vertical-align: top;
  color: #818181;
  position: relative;
}

.cate .list table td:first-child {
  width: 12%;
  vertical-align: middle;
}

.cate .list table td:nth-child(2) {
  width: 47%;
}

.cate .list table td:nth-child(3) {
  width: 100px;
}

.cate .list table td:nth-child(4) {
  width: 14%;
}

.cate .list table td > a {
  width: 50px;
  float: left;
}

.cate .list table td:first-child > a {
  width: auto;
  position: relative;
  color: #288ad6;
  top: 0;
}

.cate .list table td > a > img {
  display: block;
  max-width: 100%;
}

.cate .list table td > a > img.small {
  max-width: 70%;
  margin: auto;
}
table {
  width: 100%;
}
.cate .list table td > div {
  margin-left: 60px;
  min-height: 50px;
}

.cate .list table td > div > * {
  display: block;
  margin-bottom: 3px;
}

.cate .list table td > div .deliverytime {
  font-size: 13px;
}

.cate .list table td > div > a {
  color: #333;
  line-height: 1.5;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

.cate .list table td i {
  font-style: normal;
  display: block;
}

.cate .list table td i.ischecksaleprice {
  color: #00bd9f;
}

.cate .list table td > div > i {
  font-size: 12px;
  color: #666;
  font-style: normal;
}

.cate .list table td i.success {
  color: #289666;
}

.cate .list table td i.fail {
  color: #d0021b;
}

.cate .list table td > div > div {
  margin-top: 5px;
}

.cate .list table td > div > div > a {
  display: inline-block;
  vertical-align: top;
  color: #288ad6;
  font-size: 13px;
  padding-right: 20px;
}

.cate .list table td > b {
  font-weight: normal;
  color: #d0021b;
}

.cate .list table td > div.link {
  display: flex;
  align-items: center;
  height: auto;
  min-height: unset;
  grid-gap: 10px;
}

.cate .list table td > div.link a {
  font-size: 13px;
  color: #288ad6;
}

.cate .list a.viewmore {
  color: #288ad6;
  text-align: center;
  display: block;
  width: 300px;
  border: 1px solid #288ad6;
  border-radius: 3px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  margin: 25px auto;
}

.cate .list a.viewmore:after {
  content: "";
  width: 0;
  height: 0;
  border-top: 6px solid #288ad6;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  display: inline-block;
  vertical-align: middle;
  margin-left: 5px;
}
